<template>
    <div class='app-scroll-content'>
        <div class='app-scroll-scroll'>
       	    <div class="app-scroll-loading"></div>
            <!-- <div class="app-scroll-toploading">{{topText}}</div> -->
            <div class='app-scroll-item' v-for="(item,index) in dataList" :key="index">{{item.name}}</div>
            <div class="app-scroll-bottomloading">{{bottomText}}</div>
        </div>
    </div>
</template>
<script>
import Bscroll from 'better-scroll'
import {recommend} from "@/api/app/data.js"
export default {
    name: 'text3',
    data () {
        return {
            topText:"...",
            bottomText:"...",
            pageNo: 1,              //
            totalPage: 10,          //
            isPullDown: true,
            dataList:[],            //
            options: {
                probeType: 3,
                pullDownRefresh: {
                    threshold: 40, // 
                    stop: 30       // 
                },
                pullUpLoad: {
                    threshold: 20  // 
                },
                startY: 0
            }
        }
    },
    created () {
        this.requestData()
    },
    mounted () {
        this.$nextTick(() => {
            this.initScroll()
        })
    },
    methods: {
        initScroll () {
            this.myScroll = new Bscroll('.app-scroll-content', this.options)
            this.pullDownEvent()
            this.pullUpLoadEvent()
        },
        requestData () {
            var that = this;
            recommend(1,that.pageNo).then(res => {
                if (that.isPullDown) {
                    that.dataList = [] // 
                    that.dataList = res.data.data;
                }else{
                    var newData = that.dataList.concat(res.data.data);
                    that.dataList = newData;
                }
                that.$nextTick(() => {
                    that.finishPulling();
                })
            })
        },
        finishPulling() {
            this.myScroll.finishPullDown() // 
            this.myScroll.finishPullUp()   // 
            this.myScroll.refresh()        // dombetter-scroll
            this.bottomText = "...";
            this.topText = "";
        },
        pullDownEvent () {
            this.topText = "...";
            this.myScroll.on('pullingDown', () => {
                // console.log('')
                this.topText = "...";
                this.isPullDown = true
                this.pageNo = 1
                this.requestData()
            })
        },
        pullUpLoadEvent () {
            this.myScroll.on('pullingUp', () => {
                // console.log('');
                this.pageNo++;
                this.isPullDown = false;

                if(this.pageNo < this.totalPage){
                    this.requestData()
                }else{
                    this.bottomText = "!"
                }
            })
        }
    }

}
</script>
<style>
    /* .scroll{overflow: auto} */
    
</style>



